export const prdStatCdArray = makeJsonToArray(prdStatCd);

export const prdSelCdArray = makeJsonToArray(prdSelCd);

export function makeJsonToArray(json) {
    let result = [];
    for(let item in json) {
        result.push({'key': item , 'value': json[item]});
    }
    return result;
}